export default {
	data() {
		return {
			banner: [],
			course: []
		}
	},
	onLoad() {
		this.getAdImage()
		this.getIndexCourse()
	},
	onPullDownRefresh() {
		console("refresh");
	},
	methods: {
		getAdImage() {
			this.$api.post('index!ajaxGetAdImage.action').then(res => {
				if (res.res.status == 0) {
					this.banner = res.inf.arr
				}
			})
		},
		getIndexCourse() {
			this.$api.post('index!ajaxGetIndexCourse.action').then(res => {
				if (res.res.status == 0) {
					this.course = res.inf.arr
				}
			})
		},

		itemClcik(e) {
			uni.navigateTo({
				url: './videoDetail',
				success() {
					console.log('jumptovideodetail');
				}
			})

		}
	}
}
